import React, { useEffect } from 'react'
import { Routes, Route, HashRouter } from 'react-router-dom'
import LandingPage from '../pages/LandingPage'
import FederalFunds from '../pages/FederalFunds'
import FederalFundsDev from '../pages/dev/FederalFundsDev'
import ICIP from '../pages/ICIP'
import CapitalOutlay from '../pages/CapitalOutlay'
import JuniorBill from '../pages/JuniorBill'
import FoodInitiative from '../pages/FoodInitiative'
import BIL from '../pages/BIL'
import DFALandingPage from '../pages/DFALandingPage'
import ICIPTable from '../pages/dev/ICIPTable'
import GovLandingPage from '../pages/GovLandingPage'

export const HOST_URL = "https://nmdfa-dashboards.github.io/"

const THEMES = {
    light: "light",
    lightYellow: "lightYellow",
}
let currentTheme = THEMES.light
export { THEMES, currentTheme }

export default function App() {

    useEffect(() => {
        window.addEventListener("mavQ:setLightTheme", handleSetLightThemeEvent)
        window.addEventListener("mavQ:setYellowTheme", handleSetYellowThemeEvent)
    }, [])

    function handleSetLightThemeEvent() {
        currentTheme = THEMES.light
    }

    function handleSetYellowThemeEvent() {
        currentTheme = THEMES.lightYellow
    }

    return (
        <HashRouter>
            <Routes>
                <Route path="/" element={<LandingPage />} />
                <Route path="/dfa-dashboards" element={<DFALandingPage />} />
                <Route path="/gov-dashboards" element={<GovLandingPage />} />
                <Route path="/federalfunds" element={<FederalFunds />} />
                <Route path="/dev/federalfunds" element={<FederalFundsDev />} />
                <Route path="/dev/icip-table" element={<ICIPTable />} />
                <Route path="/icip" element={<ICIP />} />
                <Route path="/capitaloutlay" element={<CapitalOutlay />} />
                <Route path="/juniorbill" element={<JuniorBill />} />
                <Route path="/hunger" element={<FoodInitiative />} />
                <Route path="/bil" element={<BIL />} />
                <Route path="*" element={<PageNotFound />} />
            </Routes>
        </HashRouter>
    )
}

function PageNotFound() {
    return (
        <div>
            Page not found.<br></br>
            <a href={HOST_URL}>Return to the homepage</a>
        </div>
    )
}