import { faGripLines } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import React, { useState, useEffect, useRef } from 'react'
import * as style from '../../styles/ColumnsMenu.module.scss'
import { tableColumnsIcon } from '../../styles/Table.module.scss'
import { IColumn } from './Table'

export default function ColumnsMenu() {
    const [columns, setColumns] = useState([])
    const [isMenuOpen, setIsMenuOpen] = useState(false)
    const menu = useRef<HTMLDivElement>(null)

    useEffect(() => {
        function onRecievedDataFromTableau(event: any) {
            setColumns(event.detail.columns)
        }
        window.addEventListener('mavQ:toggleColumnsMenu', onMenuToggle)
        window.addEventListener('mavQ:recievedDataFromTableau', onRecievedDataFromTableau)

        return () => {
            window.removeEventListener('mavQ:toggleColumnsMenu', onMenuToggle)
            window.removeEventListener('mavQ:recievedDataFromTableau', onRecievedDataFromTableau)
        }
    })

    useEffect(() => {
        if (!isMenuOpen) {
            if (menu.current) menu.current.style.opacity = '0'
            return
        }
        if (menu.current) menu.current.style.opacity = '1'
        
        window.addEventListener('click', onClickAnywhere)
        return () => {
            window.removeEventListener('click', onClickAnywhere)
        }
    }, [isMenuOpen])

    function onClickAnywhere(event: MouseEvent) {
        const tableColumnsIconElement = document.getElementById(tableColumnsIcon)
        const tableColumnsIconChildren = tableColumnsIconElement.children
        for (let i = 0; i < tableColumnsIconChildren.length; i++) {
            if (tableColumnsIconChildren[i] === event.target) return
        }
        if (tableColumnsIconElement === event.target) return

        if (menu.current && !menu.current.contains(event.target as Node)) {
            setIsMenuOpen(false)
            menu.current.style.opacity = '0'
        }
    }

    function onMenuToggle() {
        if (columns.length === 0) return
        if (isMenuOpen) setIsMenuOpen(false)
        else setIsMenuOpen(true)
    }

    return (
        <>
            { isMenuOpen &&
                <div ref={menu} className={style.container}>
                    {
                        columns.map((column: IColumn) => {
                            return <ColumnSelect key={column.name} columnName={column.name} />
                        })
                    }
                </div>
            }
        </>
    )
}

function ColumnSelect(props: any) {
    const [columnName, setColumnName] = useState(props.columnName)
    const [isEnabled, setIsEnabled] = useState(true)

    useEffect(() => {
        const localColumnEnabledMap = localStorage.getItem('columnEnabledMap')
        if (localColumnEnabledMap) setIsEnabled(JSON.parse(localColumnEnabledMap)[columnName])
        setColumnName(props.columnName)
    })
    
    function onCheckboxClick() {
        setIsEnabled(!isEnabled)
        const localColumnEnabledMap = JSON.parse(localStorage.getItem('columnEnabledMap'))
        if (!isEnabled) localColumnEnabledMap[columnName] = true
        else localColumnEnabledMap[columnName] = false
        localStorage.setItem('columnEnabledMap', JSON.stringify(localColumnEnabledMap))
        window.dispatchEvent(new CustomEvent('mavQ:updateColumnEnabledMap', {
            detail: { columnName: columnName, enabled: !isEnabled }
        }))
    }

    return (
        <div className={style.columnSelect}>
            <div className={style.checkMark}>
                <input type="checkbox" checked={isEnabled} onChange={onCheckboxClick} />
            </div>
            {/* <FontAwesomeIcon className={style.gripLines} icon={faGripLines} /> */}
            <label>{columnName}</label>
        </div>
    )
}
