import React, { useEffect, useRef } from "react"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faFilePdf, faFileExcel, faFilePowerpoint, faBackward, faForward, faShareFromSquare, faDownload, faExpand } from "@fortawesome/free-solid-svg-icons"
import * as style from "../styles/Toolbox.module.scss"
import HasTooltip from "./Tooltip"
import * as globalStyle from "../styles/global.module.scss"

export default function Toolbox(props: any) {
    const div = useRef<HTMLDivElement>(null)

    const wrapper = useRef<HTMLDivElement>(props.wrapper.current)

    useEffect(() => {
        wrapper.current?.addEventListener("scroll", onScroll)
        window.addEventListener("scroll", onScroll)
        window.addEventListener("mavQ:resetRed", onResetRed)
        window.addEventListener("mavQ:resetWhite", onResetWhite)

        return () => {
            wrapper.current?.removeEventListener("scroll", onScroll)
            window.removeEventListener("scroll", onScroll)
            window.removeEventListener("mavQ:resetRed", onResetRed)
            window.removeEventListener("mavQ:resetWhite", onResetWhite)
        }
    }, [])

    function onScroll() {
        if (window.scrollY > 0 && div.current) {
            div.current.style.boxShadow = "-4px 4px 10px 4px rgba(0, 0, 0, .18)"
            div.current.style.borderRadius = "0 0 0 12px"
        }
        else {
            div.current.style.boxShadow = "none"
            div.current.style.borderRadius = "0 12px 0 12px"
        }
    }

    function onResetRed() {
        document.getElementById("resetDashboard").style.backgroundColor = "#ffc9c9"
    }

    function onResetWhite() {
        document.getElementById("resetDashboard").style.backgroundColor = "white"
    }

    return (
        <div ref={div} id={style.container}>
            <div className={style.shelf}>
                <HasTooltip element={<FontAwesomeIcon className={style.tool} id="undo" icon={faBackward} onClick={onUndoClick} />}
                    tooltip="Undo" />
                <HasTooltip element={<FontAwesomeIcon className={style.tool} id="redo" icon={faForward} onClick={onRedoClick} />}
                    tooltip="Redo" />
                <HasTooltip element={<FontAwesomeIcon className={style.tool} id="share" icon={faShareFromSquare} onClick={onShareClick} />}
                    tooltip="Share" />
                <HasTooltip element={<FontAwesomeIcon className={style.tool} id="download" icon={faDownload} onClick={onDownloadClick} />}
                    tooltip="Download" />
                <HasTooltip element={<FontAwesomeIcon className={style.tool} id="fullscreen" icon={faExpand} onClick={() => window.dispatchEvent(new Event("mavQ:onFullscreenClick"))} />}
                    tooltip="Fullscreen" />
            </div>
            <button id="resetDashboard" className={style.resetDashboard} onClick={onResetClicked}><p></p>RESET</button>
            <div className={style.shelf}>
                <HasTooltip element={<FontAwesomeIcon id={style.faFilePdf} className={style.icon} icon={faFilePdf} onClick={downloadPDF} />}
                    tooltip="Download PDF" />
                <HasTooltip element={<FontAwesomeIcon id={style.faFileExcel} className={style.icon} icon={faFileExcel} onClick={downloadExcel} />}
                    tooltip="Download Excel" />
                <HasTooltip element={<FontAwesomeIcon id={style.faFilePowerpoint} className={style.icon} icon={faFilePowerpoint} onClick={downloadPowerpoint} />}
                    tooltip="Download Powerpoint" />
            </div>
        </div>
    )
    function onResetClicked() {
        window.dispatchEvent(new Event("mavQ:resetViz"))
        let resetButton = document.getElementById("resetDashboard")
        resetButton.style.backgroundColor = "white";
    }

    function downloadPDF() {
        window.dispatchEvent(new Event("mavQ:showExportPDFDialog"))
    }

    function downloadExcel() {
        window.dispatchEvent(new Event("mavQ:exportCrossTabToExcel"))
    }

    function downloadPowerpoint() {
        window.dispatchEvent(new Event("mavQ:showExportPowerPointDialog"))
    }

    function onShareClick() {
        window.dispatchEvent(new Event("mavQ:showShareDialog"))
    }

    function onDownloadClick() {
        window.dispatchEvent(new Event("mavQ:showDownloadDialog"))
    }

    function onUndoClick() {
        window.dispatchEvent(new Event("mavQ:undoAsync"))
    }

    function onRedoClick() {
        window.dispatchEvent(new Event("mavQ:redoAsync"))
    }
}