 @use "global.module.scss" as *;

 $mobileScale: .6;

 #container {
     background-color: white;
     transform-origin: top right;
     margin: 0 0 0 auto;
     padding: 3px 9px;
     position: sticky;
     top: 0;
     display: flex;
     flex-direction: row;
     border-radius: 0 12px 0 12px;
     z-index: 1;
 }

 .shelf {
     background-color: white;
     display: flex;
     flex-direction: row;
     justify-content: space-evenly;
     align-items: center;
     border-radius: 12px;
 }

 .icon {
     position: relative;
     padding: 6px;
     width: 39px;
     height: 39px;
     border-radius: 12px;
     cursor: pointer;
     transition: all 0.1s ease-in-out;

     &:hover {
         background-color: $toolHover;
         scale: 1.1;
         transition: all 0.1s ease-in-out;
     }

     &:active {
         background-color: $toolActive;
         scale: 1;
         transition: all 0.1s ease-in-out;
     }

     @media screen and (max-width: 768px) {
         width: calc(39px * $mobileScale);
         height: calc(39px * $mobileScale);
     }
 }

 #faFilePdf {
     color: #973126;

     &:hover {
         color: $primaryColor;
     }
 }

 #faFileExcel {
     color: #0a9544;

     &:hover {
         color: $primaryColor;
     }
 }

 #faFilePowerpoint {
     color: #e74c3c;

     &:hover {
         color: $primaryColor;
     }
 }

 .tool {
     color: $toolColor;
     position: relative;
     padding: 9px;
     width: 30px;
     height: 30px;
     border-radius: 12px;
     cursor: pointer;
     transition: all 0.1s ease-in-out;

     &:hover {
         background-color: $toolHover;
         color: $primaryColor;
         scale: 1.1;
         transition: all 0.1s ease-in-out;
     }

     &:active {
         background-color: $toolActive;
         color: #005077;
         scale: 1;
         transition: all 0.1s ease-in-out;
     }

     @media screen and (max-width: 768px) {
         width: calc(30px * $mobileScale);
         height: calc(30px * $mobileScale);
     }
 }

 .resetDashboard {
     color: black;
     background-color: white;
     margin: 0 12px;
     height: 45px;
     width: 150px;
     display: flex;
     flex-direction: row;
     justify-content: center;
     align-items: center;
     font-weight: bold;
     font-size: 14pt;
     border: none;
     border-radius: 12px;
     box-shadow: 0 1px 2px 1px rgba(0, 0, 0, .18);
     cursor: pointer;
     user-select: none;
     transition: all 0.1s ease-in-out;

     &:hover {
         scale: 1.02;
         box-shadow: 0 2px 3px 1px rgba(0, 0, 0, .24);
         transition: all .1s ease-in-out;
     }

     &:active {
         scale: 1;
         box-shadow: inset 0 2px 2px 1px rgba(0, 0, 0, .18);
         transition: all 0.1s ease-in-out;
     }

     p {
         font-size: 24px;
         margin: -3px 12px 0 0;
     }

     @media screen and (max-width: 768px) {
         height: calc(45px * 0.8);
         width: calc(150px * 0.8);
         font-size: 12pt;

         p {
             font-size: calc(24px * $mobileScale);
         }
     }
 }