import React, { useEffect, useRef, useState } from "react"
import Navbar from "./Navbar"
import * as style from "../../styles/Sidebar.module.scss"
import * as globalStyle from "../../styles/global.module.scss"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faChevronRight, faChevronLeft } from "@fortawesome/free-solid-svg-icons"
import { isMobile } from "react-device-detect"

export default function Sidebar(props: any) {
    const { codes, tabNames, starredCodes, downloads } = props
    const [isSidebarOpen, setIsSidebarOpen] = useState<boolean>(false)
    const [isMobileClient, setIsMobileClient] = useState<boolean>(!isMobile)
    const [isFirstTableauVizLoaded, setIsFirstTableauVizLoaded] = useState<boolean>(false)

    const mobileSiderbar = useRef<HTMLDivElement>(null)

    useEffect(() => {
        window.addEventListener("mavQ:firstTableauVizLoaded", handleFirstTableauVizLoaded)

        window.addEventListener("mavQ:setLightTheme", handleSetLightThemeEvent)
        window.addEventListener("mavQ:setYellowTheme", handleSetYellowThemeEvent)
        window.addEventListener("mavQ:closeSidebar", closeMobileSidebar)

        if (isMobile) {
            setIsMobileClient(true)
            return
        }

        handleResize()
        window.addEventListener("resize", handleResize)

        return () => {
            window.removeEventListener("mavQ:firstTableauVizLoaded", handleFirstTableauVizLoaded)
            window.removeEventListener("mavQ:setLightTheme", handleSetLightThemeEvent)
            window.removeEventListener("mavQ:setYellowTheme", handleSetYellowThemeEvent)
            window.removeEventListener("mavQ:closeSidebar", closeMobileSidebar)
            window.removeEventListener("resize", handleResize)
        }
    })

    useEffect(() => {
        if (isMobileClient) mobileSiderbar.current.style.opacity = "1"
    }, [isMobileClient])

    useEffect(() => {
        if (!mobileSiderbar.current) return
        if (isSidebarOpen) openSidebar()
        else closeMobileSidebar()
    }, [isSidebarOpen])

    function handleResize() {
        if (window.innerWidth < 768) {
            setIsMobileClient(true)
            window.dispatchEvent(new CustomEvent("mavQ:sidebarToggle", { detail: { enabled: true } }))
        } else {
            setIsMobileClient(false)
            window.dispatchEvent(new CustomEvent("mavQ:sidebarToggle", { detail: { enabled: false } }))
        }
    }

    function handleFirstTableauVizLoaded() {
        setIsFirstTableauVizLoaded(true)
    }

    function handleSetLightThemeEvent() {
        document.getElementById(style.desktopContainer).style.backgroundColor = "white"
    }

    function handleSetYellowThemeEvent() {
        document.getElementById(style.desktopContainer).style.backgroundColor = globalStyle.lightYellow
    }

    function openSidebar() {
        mobileSiderbar.current.style.left = "0"
        mobileSiderbar.current.style.opacity = "1"
    }

    function closeMobileSidebar() {
        setIsSidebarOpen(false)
        mobileSiderbar.current.style.left = "-240px"
        mobileSiderbar.current.style.opacity = "unset"
    }

    return (
        <>
            {isMobileClient &&
                <div ref={mobileSiderbar} id={style.mobileContainer} onMouseEnter={() => setIsSidebarOpen(true)} onMouseLeave={() => setIsSidebarOpen(false)}>
                    {isMobile && <FontAwesomeIcon icon={isSidebarOpen ? faChevronLeft : faChevronRight} className={style.arrow} onClick={() => setIsSidebarOpen(true)} />}
                    {!isMobile && <FontAwesomeIcon icon={isSidebarOpen ? faChevronLeft : faChevronRight} className={style.arrow} />}
                    <SidebarContent codes={codes} tabNames={tabNames} starredCodes={starredCodes} downloads={downloads} isFirstTableauVizLoaded={isFirstTableauVizLoaded} />
                </div>
            }

            {!isMobileClient && 
                <div id={style.desktopContainer}>
                    <SidebarContent codes={codes} tabNames={tabNames} starredCodes={starredCodes} downloads={downloads} isFirstTableauVizLoaded={isFirstTableauVizLoaded} />
                </div>
            }
        </>
    )
}

import Feedback from "./Feedback"
export function SidebarContent(props: any) {
    const { codes, tabNames, starredCodes, downloads, isFirstTableauVizLoaded } = props

    return (
        <div id={style.sidebar}>
            <Navbar codes={codes} tabNames={tabNames} starredCodes={starredCodes} isFirstTableauVizLoaded={isFirstTableauVizLoaded} />
            {/* <Feedback /> */}
        </div>
    )
}