import React, { forwardRef, useEffect, useImperativeHandle, useRef, useState } from "react"
import * as style from "../styles/Tooltip.module.scss"

export default function HasTooltip(props: any) {
    const [element, setElement] = useState<JSX.Element>(props.element)
    const [tooltipContent, setTooltipContent] = useState<string>(props.tooltip)

    const div = useRef<HTMLDivElement>(null)
    const tooltip = useRef(null)

    useEffect(() => {
        if (!div.current) return
        div.current.addEventListener("mouseenter", showTooltip)
        return () => {
            if (!div.current) return
            div.current.removeEventListener("mouseenter", showTooltip)
        }
    })

    function showTooltip(event: any) {
        if (!tooltip.current) return
        tooltip.current.showTooltip()
        div.current.addEventListener("mouseleave", onMouseLeave)
    }

    function onMouseLeave() {
        tooltip.current.hideTooltip()
        div.current.removeEventListener("mouseleave", onMouseLeave)
    }

    return (
        <div ref={div} className={style.hasTooltip}>
            {element}
            <Tooltip ref={tooltip} content={tooltipContent} />
        </div>
    )
}

export const Tooltip = React.forwardRef((props: any, ref: any) => {
    const [content, setContent] = useState<string>(props.content)
    const div = useRef<HTMLDivElement>(null)

    useImperativeHandle(ref, () => ({
        showTooltip: onShowTooltip,
        hideTooltip: onHideTooltip,
    }))

    useEffect(() => {
        window.addEventListener("mavQ:onShowTooltip", onShowTooltip)
        window.addEventListener("mavQ:onHideTooltip", onHideTooltip)

        return () => {
            window.removeEventListener("mavQ:onShowTooltip", onShowTooltip)
            window.removeEventListener("mavQ:onHideTooltip", onHideTooltip)
        }
    })

    return (
        <div ref={div} className={style.tooltip}>
            <p>{content}</p>
        </div>
    )

    function onShowTooltip() {
        if (!div.current) return
        div.current.style.opacity = "1"
        checkAndFixTooltipOverflow()
    }

    function onHideTooltip() {
        if (!div) return
        div.current.style.opacity = "0"
        setTimeout(() => {
            div.current.style.left = "unset"
            div.current.style.right = "unset"
            div.current.style.bottom = "-39px"
        }, 500)
    }

    function checkAndFixTooltipOverflow() {
        const tooltipRect = div.current.getBoundingClientRect()

        if (tooltipRect.right > window.innerWidth) {
            div.current.style.right = "0"
            div.current.style.left = "unset"
        }
        else if (tooltipRect.left < 0) {
            div.current.style.left = "0"
            div.current.style.right = "unset"
        }
        else {
            div.current.style.left = "unset"
            div.current.style.right = "unset"
        }

        if (tooltipRect.bottom > window.innerHeight) {
            div.current.style.bottom = "0"
        }
        else
            div.current.style.bottom = "-39px"
    }
})