import React, { useState, useEffect, useRef, useImperativeHandle } from "react"
import * as style from "../styles/VizContainer.module.scss"
import * as Tableau from "tableau-api-js"

import { IColumn } from './table/Table'

export const TableauViz = React.forwardRef((props: any, ref: any) => {
    const [index, setIndex] = useState(props.index)
    const [codes, setCodes] = useState(props.codes)
    const [urls, setUrls] = useState(props.urls)
    const [heights, setHeights] = useState(props.heights)
    const [width, setWidth] = useState(props.width)
    
    const [tableCode, setTableCode] = useState(props.tableCode)
    const [tableTitle, setTableTitle] = useState(props.tableTitle)

    const vizPlaceholder = useRef<HTMLDivElement>(null)
    const viz = useRef<Tableau.Viz>(null)
    const currentUrl = useRef<string>(urls[index])
    const hasChanges = useRef<boolean>(false)

    const tableOptions = {
        maxRows: 0,
        ignoreAliases: false,
        ignoreSelection: true,
        includeAllColumns: false
    }

    const vizOptions = {
        hideTabs: true,
        width: width + "px",
        height: heights[index] + "px",
        onFirstInteractive: async function () {
            if (index === 0) window.dispatchEvent(new CustomEvent("mavQ:firstTableauVizLoaded"))
            if (tableCode !== codes[index]) return
            const sheet = viz.current.getWorkbook().getActiveSheet().getWorksheets().get(tableTitle)
            await sheet.getUnderlyingTablesAsync().then(tables => {
                return sheet.getUnderlyingTableDataAsync(tables[0].getTableId(), tableOptions).then(getUnderlyingData)
            })
        }
    }

    useImperativeHandle(ref, () => ({
        initializeTableauViz: initializeTableauViz,
        resetViz: resetViz,
        handleResetButton: handleResetButton,
        showExportPDFDialog: showExportPDFDialog,
        exportCrossTabToExcel: exportCrossTabToExcel,
        showExportPowerPointDialog: showExportPowerPointDialog,
        showShareDialog: showShareDialog,
        showDownloadDialog: showDownloadDialog,
        undoAsync: undoAsync,
        redoAsync: redoAsync,
        getCurrentUrl: getCurrentUrl,
    }))

    return <div ref={vizPlaceholder} id={style.vizPlaceholder}></div>

    function initializeTableauViz() {
        if (index === 0) console.log("initializeTableauViz 01")
        if (viz.current) disposeViz()
        setVizPlaceholderInlineStyles()
        viz.current = new Tableau.Viz(vizPlaceholder.current, urls[index], vizOptions)
        viz.current.addEventListener('filterchange', onVizActionDetected);
        viz.current.addEventListener('marksselection', onVizActionDetected);
        viz.current.addEventListener('parametervaluechange', onVizActionDetected);
        viz.current.addEventListener('tabswitch', onTabSwitch);
        currentUrl.current = urls[index]
        hasChanges.current = false
        handleResetButton()
    }

    function disposeViz() {
        viz.current.removeEventListener('filterchange', onVizActionDetected);
        viz.current.removeEventListener('marksselection', onVizActionDetected);
        viz.current.removeEventListener('parametervaluechange', onVizActionDetected);
        viz.current.removeEventListener('tabswitch', onTabSwitch);
        viz.current.dispose()
    }

    function setVizPlaceholderInlineStyles() {
        vizPlaceholder.current.style.minWidth = `${width}px`
        vizPlaceholder.current.style.maxWidth = `${width}px`
        vizPlaceholder.current.style.height = `${heights[index]}px`
    }

    function onVizActionDetected(event: any) {
        window.dispatchEvent(new Event("mavQ:resetRed"))
        hasChanges.current = true
    }

    function onTabSwitch(event: any) {
        currentUrl.current = viz.current.getWorkbook().getActiveSheet().getUrl();
        const urlIndex = urls.indexOf(currentUrl.current)
        vizPlaceholder.current.style.height = heights[urlIndex] + "px"
        window.dispatchEvent(new CustomEvent("mavQ:tableauTabSwitch", { detail: { code: codes[urlIndex] } }))
    }

    function resetViz() {
        hasChanges.current = false
        if (currentUrl.current !== urls[index]) {
            const currentUrlIndex = urls.indexOf(currentUrl.current)
            currentUrl.current = urls[index]
            window.dispatchEvent(new CustomEvent("mavQ:resetViz"))
            window.dispatchEvent(new CustomEvent("mavQ:toggleTableauViz", { detail: { code: codes[currentUrlIndex] } }))
            window.dispatchEvent(new CustomEvent("mavQ:tableauTabSwitch", { detail: { code: codes[currentUrlIndex] } }))
        }
        initializeTableauViz()
    }

    function handleResetButton() {
        if (hasChanges.current === false)
            window.dispatchEvent(new CustomEvent("mavQ:resetWhite"))
        else
            window.dispatchEvent(new CustomEvent("mavQ:resetRed"))
    }

    function showExportPDFDialog() {
        viz.current.showExportPDFDialog()
        scrollToOneThirdFromTopOfViz()
    }

    function exportCrossTabToExcel(downloadSheetName: string) {
        viz.current.exportCrossTabToExcel(downloadSheetName)
    }

    function showExportPowerPointDialog() {
        viz.current.showExportPowerPointDialog()
        scrollToOneThirdFromTopOfViz()
    }

    function showShareDialog() {
        viz.current.showShareDialog()
        scrollToOneThirdFromTopOfViz()
    }

    function showDownloadDialog() {
        viz.current.showDownloadDialog()
        scrollToOneThirdFromTopOfViz()
    }

    function undoAsync() {
        viz.current.undoAsync()
    }

    function redoAsync() {
        viz.current.redoAsync()
    }

    function getCurrentUrl() {
        return currentUrl.current
    }

    function scrollToOneThirdFromTopOfViz() {
        const vizHeight = vizPlaceholder.current.offsetHeight
        const vizTop = vizPlaceholder.current.offsetTop
        const scrollPosition = vizTop + (vizHeight / 3)
        window.scrollTo({ top: scrollPosition, behavior: "smooth" })
    }

    async function getUnderlyingData(dataTable: any) {
        let data2d: any[][] = dataTable.getData()
        let columns: any[] = dataTable.getColumns()
        let iColumns: IColumn[] = columns.map((column: any, index: number) => {
            let firstValue = data2d[0][index].formattedValue
            const name = column.getFieldName()
            const type = getDataType(firstValue, name)
            const textAlign = getTextAlign(name, type)
            return {
                name: name,
                type: type,
                textAlign: textAlign
            } as IColumn
        })

        let dataArray = []
        data2d.forEach(row => {
            let newRow: any[] = []
            row.forEach((cell: any, columnIndex: number) => {
                newRow[iColumns[columnIndex].name] = formatValue(iColumns[columnIndex].name, cell.formattedValue)
            })
            dataArray.push(newRow)
        })
        const eventDetails = { columns: iColumns, data: dataArray }
        const event = new CustomEvent("mavQ:recievedDataFromTableau", { detail: eventDetails })
        window.dispatchEvent(event)

        function getDataType(value: string, columnName: string): string {
            if (value.includes("$")) return "usd"

            if (columnName == "Year") return "year"

            value = value.replace(/[,|,|$]/g, "")
            if (isValueNumeric(value)) return "number"

            if (value.includes("http")) return "link"

            return "string"
        }

        function getTextAlign(columnName: string, type: string): string {
            const centerAlignColumns = ["Project ID", "Entity Code", "Rank", "Zip"]
            if (centerAlignColumns.includes(columnName)) return "center"
            if (type == "number") return "right"
            if (type == "usd") return "right"
            return "left"
        }
    }

    function formatValue(column: string, value: string): any {
        value = value.replace(/[,|,|$]/g, "")
        if (column == "Year") return parseInt(value)
        if (value === "0") return 0
        if (isValueNumeric(value)) return Math.round(parseFloat(value) * 100) / 100

        const nulls = ["Null", "null", "NULL", "N/A", "n/a", "N/a", "NaN", "Na", "NA"]
        if (nulls.includes(value)) return ""

        return value
    }

    function isValueNumeric(value: any) {
        return !isNaN(value) && !isNaN(parseFloat(value))
    }
})