import React, { useState, useEffect, useRef } from "react"
import * as style from "../styles/VizContainer.module.scss"
import * as globalStyle from "../styles/global.module.scss"
import { TableauViz } from "./TableauViz"

export default function VizContainer(props: any) {
    const [codes, setCodes] = useState(props.codes)
    const [heights, setHeights] = useState(props.heights)
    const [width, setWidth] = useState(props.width)
    const [urls, setUrls] = useState(props.urls)
    const [downloads, setDownloads] = useState(props.downloads)
    const [leftOffset, setLeftOffset] = useState(props.leftOffset)
    const [tableCode, setTableCode] = useState(props.tableCode)
    const [tableTitle, setTableTitle] = useState(props.tableTitle)

    const tableauContainer = useRef<HTMLDivElement>(null)
    const currentVizIndex = useRef<number>(0)
    const vizRefs = {}
    for (let i = 0; i < codes.length; i++) {
        vizRefs[i] = useRef(null)
    }

    function setTableauContainerInlineStyles() {
        tableauContainer.current.style.left = `${leftOffset}px`
        tableauContainer.current.style.margin = `0 ${leftOffset}px 0 0`
        tableauContainer.current.style.width = `${width}px`
        tableauContainer.current.style.height = `${heights[0]}px`
    } 

    useEffect(() => {
        setTableauContainerInlineStyles()
        if (vizRefs[0].current) vizRefs[0].current.initializeTableauViz()
        window.addEventListener("mavQ:firstTableauVizLoaded", handleFirstTableauVizLoaded)
        window.addEventListener("mavQ:toggleTableauViz", handleToggleTableauViz)
        window.addEventListener("mavQ:tableauTabSwitch", handleTabSwitch)

        window.addEventListener("mavQ:resetViz", handleResetViz)
        window.addEventListener("mavQ:showExportPDFDialog", handleShowExportPDFDialog)
        window.addEventListener("mavQ:exportCrossTabToExcel", handleExportCrossTabToExcel)
        window.addEventListener("mavQ:showExportPowerPointDialog", handleShowExportPowerPointDialog)
        window.addEventListener("mavQ:showShareDialog", handleShowShareDialog)
        window.addEventListener("mavQ:showDownloadDialog", handleShowDownloadDialog)
        window.addEventListener("mavQ:undoAsync", handleUndoAsync)
        window.addEventListener("mavQ:redoAsync", handleRedoAsync)

        window.addEventListener("mavQ:setLightTheme", handleSetLightThemeEvent)
        window.addEventListener("mavQ:setYellowTheme", handleSetYellowThemeEvent)

        return () => {
            window.removeEventListener("mavQ:firstTableauVizLoaded", handleFirstTableauVizLoaded)
            window.removeEventListener("mavQ:toggleTableauViz", handleToggleTableauViz)
            window.removeEventListener("mavQ:tableauTabSwitch", handleTabSwitch)
            window.removeEventListener("mavQ:resetViz", handleResetViz)
            window.removeEventListener("mavQ:showExportPDFDialog", handleShowExportPDFDialog)
            window.removeEventListener("mavQ:exportCrossTabToExcel", handleExportCrossTabToExcel)
            window.removeEventListener("mavQ:showExportPowerPointDialog", handleShowExportPowerPointDialog)
            window.removeEventListener("mavQ:showShareDialog", handleShowShareDialog)
            window.removeEventListener("mavQ:showDownloadDialog", handleShowDownloadDialog)
            window.removeEventListener("mavQ:undoAsync", handleUndoAsync)
            window.removeEventListener("mavQ:redoAsync", handleRedoAsync)
            window.removeEventListener("mavQ:setLightTheme", handleSetLightThemeEvent)
            window.removeEventListener("mavQ:setYellowTheme", handleSetYellowThemeEvent)
        }
    })

    return (
        <div ref={tableauContainer} id={style.tableauContainer}>
            {
                codes.map((code: string, index: number) => {
                    return <TableauViz ref={vizRefs[index]} key={code} index={index} codes={codes} heights={heights} width={width} urls={urls} tableauContainer={tableauContainer} tableCode={tableCode} tableTitle={tableTitle} />
                })
            }
        </div>
    )

    function handleFirstTableauVizLoaded() {
        window.dispatchEvent(new Event("resize"))
        for (let i = 1; i < codes.length; i++) {
            vizRefs[i].current.initializeTableauViz()
        }
    }

    function handleToggleTableauViz(event: any) {
        if (event.detail.code === codes[currentVizIndex.current]) vizRefs[currentVizIndex.current].current.initializeTableauViz()
        currentVizIndex.current = codes.indexOf(event.detail.code)

        const x = currentVizIndex.current * width
        tableauContainer.current.style.height = `${heights[currentVizIndex.current]}px`
        tableauContainer.current.scrollTo({ left: x, behavior: 'auto' })
        window.dispatchEvent(new Event("mavQ:scrollToTop"))
        vizRefs[currentVizIndex.current].current.handleResetButton()
    }

    function handleTabSwitch(event: any) {
        const index = codes.indexOf(event.detail.code)
        tableauContainer.current.style.height = `${heights[index]}px`
        tableauContainer.current.scrollIntoView({ behavior: 'smooth', block: 'start' })
    }

    function handleResetViz() {
        vizRefs[currentVizIndex.current].current.resetViz()
    }

    function handleShowExportPDFDialog() {
        vizRefs[currentVizIndex.current].current.showExportPDFDialog()
    }

    function handleExportCrossTabToExcel() {
        const vizUrl = vizRefs[currentVizIndex.current].current.getCurrentUrl()
        const downloadIndex = urls.indexOf(vizUrl)
        vizRefs[currentVizIndex.current].current.exportCrossTabToExcel(downloads[downloadIndex])
    }

    function handleShowExportPowerPointDialog() {
        vizRefs[currentVizIndex.current].current.showExportPowerPointDialog()
    }

    function handleShowShareDialog() {
        vizRefs[currentVizIndex.current].current.showShareDialog()
    }

    function handleShowDownloadDialog() {
        vizRefs[currentVizIndex.current].current.showDownloadDialog()
    }

    function handleUndoAsync() {
        vizRefs[currentVizIndex.current].current.undoAsync()
    }

    function handleRedoAsync() {
        vizRefs[currentVizIndex.current].current.redoAsync()
    }

    function handleSetLightThemeEvent() {
        document.getElementById(style.tableauContainer).style.backgroundColor = "white"
    }

    function handleSetYellowThemeEvent() {
        document.getElementById(style.tableauContainer).style.backgroundColor = globalStyle.lightYellow
    }
}