import React, { useEffect, useRef, useState } from "react"
import Footer from "../Footer"
import * as style from "../../styles/TableDashboard.module.scss"
import * as globalStyle from "../../styles/global.module.scss"
import Sidebar from "../sidebar/Sidebar"
import Alert from "../Alert"
import ReviewForm from "../ReviewForm"
import VizContainer from "../VizContainer"
import Table from "./Table"

export default function TableDashboard(props: any) {

    const [codes, setCodes] = useState(props.codes)
    const [starredCodes, setStarredCodes] = useState(props.starredCodes)
    const [tabNames, setTabNames] = useState(props.tabNames)
    const [heights, setHeights] = useState(props.heights)
    const [urls, setUrls] = useState(props.urls)
    const [downloads, setDownloads] = useState(props.downloads)
    const [width, setWidth] = useState(props.width)
    const [leftOffset, setLeftOffset] = useState(props.leftOffset)
    const [tableCode, setTableCode] = useState(props.tableCode)
    const [tableTitle, setTableTitle] = useState(props.tableTitle)
    const [tableFilters, setTableFilters] = useState(props.tableFilters)

    const wrapper = useRef<HTMLDivElement>(null)
    const dashboard = useRef<HTMLDivElement>(null)

    useEffect(() => {
        wrapper.current.addEventListener('fullscreenchange', onFullscreenChange)
        wrapper.current.scrollIntoView()
        window.addEventListener("mavQ:setLightTheme", handleSetLightThemeEvent)
        window.addEventListener("mavQ:setYellowTheme", handleSetYellowThemeEvent)
        window.addEventListener("mavQ:onFullscreenClick", onFullscreenClick)
        window.addEventListener("resize", scaleDashboard)
        window.dispatchEvent(new Event("resize"))

        return () => {
            window.removeEventListener("resize", scaleDashboard)
            window.removeEventListener("mavQ:onFullscreenClick", onFullscreenClick)
            wrapper.current?.removeEventListener('fullscreenchange', onFullscreenChange)
        }
    })

    function handleSetLightThemeEvent() {
        document.getElementById(style.tableauBlocker).style.backgroundColor = "white"
    }

    function handleSetYellowThemeEvent() {
        document.getElementById(style.tableauBlocker).style.backgroundColor = globalStyle.lightYellow
    }

    return (
        <div ref={wrapper} id={style.wrapper}>
            <div ref={dashboard} id={style.dashboard}>
                <Alert />
                <ReviewForm />
                <div id={style.mainContent}>
                    <div id={style.topContainer}>
                        <Sidebar codes={codes} tabNames={tabNames} starredCodes={starredCodes} downloads={downloads} />
                        <VizContainer codes={codes} heights={heights} urls={urls} width={width} downloads={downloads} leftOffset={leftOffset} tableCode={tableCode} tableTitle={tableTitle} />
                        <div id={style.tableauBlocker}></div>
                    </div>
                    <Table title={tableTitle} filters={tableFilters} />
                </div>
                <Footer />
            </div>
        </div>
    )

    function scaleDashboard() {
        const minScale = .08
        const maxScale = 1
        const width = dashboard.current.clientWidth
        const height = dashboard.current.clientHeight
        const windowWidth = window.innerWidth

        let scale = windowWidth / width
        if (scale < minScale) scale = minScale
        else if (scale > maxScale) scale = maxScale
        else scale = scale

        dashboard.current.style.scale = scale + ""
        dashboard.current.style.marginRight = "-" + (width - width * scale) + "px"
        dashboard.current.style.marginBottom = "-" + (height - height * scale) + "px"
    }

    function onFullscreenClick() {
        if (document.fullscreenElement) {
            document.exitFullscreen()
            return
        }
        wrapper.current.requestFullscreen()
    }

    function onFullscreenChange() {
        const isContainerFullscreen: boolean = document.fullscreenElement ? true : false
        if (isContainerFullscreen) {
            scaleDashboard()
            dashboard.current.style.overflow = "scroll"
            return
        }
        dashboard.current.style.overflow = "hidden"
        dashboard.current.scrollTop = 0
        scaleDashboard()
    }
}
