import React, { useEffect } from "react"
import * as style from "../styles/ReviewForm.module.scss"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faX } from "@fortawesome/free-solid-svg-icons"

export default function ReviewForm() {

    useEffect(() => {
        window.addEventListener("mavQ:toggleReviewForm", handleToggleReviewFormEvent)
        return () => {
            window.removeEventListener("mavQ:toggleReviewForm", handleToggleReviewFormEvent)
        }
    }, [])

    function handleToggleReviewFormEvent() {
        const reviewForm = document.getElementById(style.wrapper)
        reviewForm.style.opacity = "1"
        reviewForm.style.pointerEvents = "unset"
    }

    function closeForm() {
        const reviewForm = document.getElementById(style.wrapper)
        reviewForm.style.opacity = "0"
        reviewForm.style.pointerEvents = "none"
    }

    function handleSubmit() {
        window.dispatchEvent(new CustomEvent("mavQ:alert", { detail: { message: "Thank you for your feedback!" } }))
        closeForm()
    }

    return (
        <div id={style.wrapper}>
            <span id={style.header}>
                <p id={style.title}>Leave a review</p>
                <FontAwesomeIcon id={style.close} icon={faX} className={style.close} onClick={closeForm} />
            </span>
            <textarea id={style.input} placeholder="..." />
            <button id={style.submit} onClick={handleSubmit}>Submit</button>
        </div>
    )
}