import React, { useRef, useState, useEffect } from "react"
import * as style from "../../styles/NavBar.module.scss"
import * as globalStyle from "../../styles/global.module.scss"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faChevronRight } from "@fortawesome/free-solid-svg-icons"
import { THEMES, currentTheme } from "../App"

export default function Navbar(props: any) {
    const { codes, tabNames, starredCodes, isFirstTableauVizLoaded } = props
    const lastCheckedInput = useRef<HTMLInputElement>(null)

    return (
        <div className={style.container}>
            {codes.map((code: string, index: number) => {
                if (starredCodes)
                    return <NavButton key={index} code={code} tabName={tabNames[index]} isStarred={starredCodes.includes(code)} isFirstTableauVizLoaded={isFirstTableauVizLoaded} />
                else
                    return <NavButton key={index} code={code} tabName={tabNames[index]} isStarred={false} isFirstTableauVizLoaded={isFirstTableauVizLoaded} />
            })}
        </div>
    )

    function NavButton(props: any) {
        const { code, tabName, isStarred, isFirstTableauVizLoaded } = props
        const checkbox = useRef<HTMLInputElement>(null)
        const colorTab = useRef<HTMLSpanElement>(null)

        useEffect(() => {
            if (code.split("_")[1] === "01" && checkbox.current) {
                lastCheckedInput.current = checkbox.current
                lastCheckedInput.current.checked = true
            }
            window.addEventListener("mavQ:tableauTabSwitch", checkNavButton)
            
            setIcipRibbons()

            return () => {
                window.removeEventListener("mavQ:tableauTabSwitch", checkNavButton)
            }
        })

        return (
            <>
                <input ref={checkbox} type="checkbox" className={style.buttonInput}></input>
                <div className={style.navButton} onClick={handleNavButtonClick}>
                    <span ref={colorTab} className={style.buttonTabColor}></span>
                    <span className={style.buttonText}>
                        <div className={style.buttonCode}>
                            {code}{isStarred ? <p className={style.buttonStar}></p> : ""}
                        </div>
                        <p className={style.buttonName}>{tabName}</p>
                    </span>
                    <span className={style.buttonArrowContainer}>
                        <FontAwesomeIcon icon={faChevronRight} className={style.buttonArrow} />
                    </span>
                </div>
            </>
        )

        function handleNavButtonClick(event: any) {
            if (!isFirstTableauVizLoaded) return
            lastCheckedInput.current.checked = false
            checkbox.current.checked = true
            lastCheckedInput.current = checkbox.current

            const code = codes[getIndex()]
            window.dispatchEvent(new CustomEvent("mavQ:toggleTableauViz", { detail: { code: code } }))

            handleIcipThemes()
        }

        function checkNavButton(event: any) {
            if (event.detail.code !== codes[getIndex()]) return
            lastCheckedInput.current.checked = false
            checkbox.current.checked = true
            lastCheckedInput.current = checkbox.current
        }

        function getIndex() {
            const inputs = checkbox.current.parentElement.querySelectorAll("input")
            const checkedIndex = Array.prototype.indexOf.call(inputs, checkbox.current)
            return checkedIndex
        }

        function setIcipRibbons() {
            if (colorTab.current === null) return
            if (code === "ICIP_01") colorTab.current.style.backgroundColor = globalStyle.darkGreen
            if (code === "ICIP_02") colorTab.current.style.backgroundColor = globalStyle.darkGreen
            if (code === "ICIP_03") colorTab.current.style.backgroundColor = globalStyle.yellow
            if (code === "ICIP_04") colorTab.current.style.backgroundColor = globalStyle.yellow
        }

        function handleIcipThemes() {
            if (code === "ICIP_03" || code === "ICIP_04")
                window.dispatchEvent(new CustomEvent("mavQ:setYellowTheme"))
            else if (currentTheme !== THEMES.light)
                window.dispatchEvent(new CustomEvent("mavQ:setLightTheme"))
        }
    }
}