import React, { useState, useEffect, useRef } from "react"
import ReactDOMServer from "react-dom/server"
import * as style from "../../styles/Table.module.scss"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faTableColumns } from "@fortawesome/free-solid-svg-icons"
import ColumnsMenu from "./ColumnsMenu"

export interface IColumn {
    name: string,
    type: string,
    textAlign: string
}

export default function Table(props: any) {
    const [title, setTitle] = useState(props.title)
    const [filters, setFilters] = useState(props.filters)
    const [columns, setColumns] = useState<IColumn[]>([])
    const [columnEnabledMap, setColumnEnabledMap] = useState({})
    const filteredData = useRef([])
    const data = useRef([])
    const currentRowIndexCount = useRef(null)
    const tableDiv = useRef(null)
    const table = useRef(null)
    const tbody = useRef(null)
    const colgroup = useRef(null)
    const tableHeader = useRef(null)
    let lastSort = useRef({ column: {}, ascending: false, sort: null })
    let currentRowIndex = 0
    let amountOfRowsToDisplay = 50
    let selectedColumnIndex = -1

    useEffect(() => {
        function onRecievedDataFromTableau(event: any) {
            initializeColumnEnabledMap(event.detail.columns)
            setColumns(event.detail.columns)
            data.current = [...event.detail.data]
            filteredData.current = [...event.detail.data]
            table.current.style.opacity = "1"
        }
        window.addEventListener("mavQ:updateColumnEnabledMap", updateColumnEnabledMap)

        window.addEventListener("mavQ:recievedDataFromTableau", onRecievedDataFromTableau)

        return () => {
            window.removeEventListener("mavQ:recievedDataFromTableau", onRecievedDataFromTableau)
            window.removeEventListener("mavQ:updateColumnEnabledMap", updateColumnEnabledMap)
        }
    })

    function initializeColumnEnabledMap(columns: IColumn[]) {
        const localColumnEnabledMap = localStorage.getItem("columnEnabledMap")
        const newColumnEnabledMap = {}
        if (localColumnEnabledMap === null) {
            columns.map((column: IColumn) => {
                newColumnEnabledMap[column.name] = true
            })
        }
        else {
            const parsedColumnEnabledMap = JSON.parse(localColumnEnabledMap)
            columns.map((column: IColumn) => {
                if (parsedColumnEnabledMap[column.name] === undefined) newColumnEnabledMap[column.name] = true
                else newColumnEnabledMap[column.name] = parsedColumnEnabledMap[column.name]
            })
        }
        localStorage.setItem("columnEnabledMap", JSON.stringify(newColumnEnabledMap))
        setColumnEnabledMap(newColumnEnabledMap)
    }

    useEffect(() => {
        if (columns.length === 0) return
        function reorderDataRows() {
            let newData = []
            data.current.forEach((row: any) => {
                let newRow = {}
                columns.forEach((column: IColumn) => newRow[column.name] = row[column.name])
                newData.push(newRow)
            })
            data.current = [...newData]
        }
        Array.from(tableHeader.current.children).forEach((th: HTMLElement) => th.removeAttribute("style"))
        reorderDataRows()
        onFilterChanged()
    }, [columns, columnEnabledMap])

    function updateColumnEnabledMap(event: any) {
        const newColumnEnabledMap = {...columnEnabledMap}
        newColumnEnabledMap[event.detail.columnName] = event.detail.enabled
        setColumnEnabledMap(newColumnEnabledMap)
    }

    return (
        <div id={style.detailedTable}>
            <p id={style.detailedTableBanner}>{title.substring(2)}</p>
            <div id={style.filters}>
                {
                    filters.map((name: string, index: number) => {
                        return (
                            <span key={index} className={style.filter}>
                                <p>{name}:</p>
                                <input type="text" id={name} className={style.filterInput} placeholder="type to search" onKeyUp={onFilterChanged} />
                            </span>
                        )
                    })
                }
                <>
                    <FontAwesomeIcon icon={faTableColumns} id={style.tableColumnsIcon} onClick={() => window.dispatchEvent(new Event("mavQ:toggleColumnsMenu"))} />
                    <ColumnsMenu />
                </>
            </div>
            <div ref={tableDiv} className={style.tableDiv}>
                <table ref={table}>
                    <colgroup ref={colgroup}>
                        {columns.map((column: IColumn, index: number) => {
                            if (!columnEnabledMap[column.name]) return null
                            return <col key={column.name} />
                        })}
                    </colgroup>
                    <thead>
                        <tr ref={tableHeader} onMouseDown={onColumnMouseDown} onMouseUp={onColumnReleased}>
                            {
                                columns.map((column: IColumn, index: number) => {
                                    if (!columnEnabledMap[column.name]) return null
                                    return <th key={"th" + column.name}>{column.name}<SortButton column={column} /></th>
                                })
                            }
                        </tr>
                    </thead>
                    <tbody ref={tbody}></tbody>
                </table>
            </div>
            <div id={style.tableControls}>
                <p id={style.amountOfRowsToDisplay}>Number of Rows:</p>
                <select id={style.rowAmountSelect} onChange={onRowAmountChange}>
                    <option value="50">50</option>
                    <option value="100">100</option>
                    <option value="250">250</option>
                    <option value="500">500</option>
                    <option value="1000">1000</option>
                </select>
                <p ref={currentRowIndexCount} id={style.currentRowIndexCount}>Loading table rows...</p>
                <button className={style.tableButton} onClick={onTableBackClick}>Back</button>
                <button className={style.tableButton} onClick={onTableNextClick}>Next</button>
                <input type="text" id={style.viewRowSearch} placeholder="Row Number" onKeyUp={onRowSearchKeyUp} />
            </div>
        </div>
    )

    function Link(props: any) {
        return <a className={style.link} href={props.value} target="_blank">{props.value}</a>
    }
    function displayRangeInHTMLTable(startIndex: number, endIndex: number) {
        if (endIndex > filteredData.current.length) endIndex = filteredData.current.length
        currentRowIndex = startIndex
        currentRowIndexCount.current.innerHTML = "Showing " + (startIndex + 1) + " to " + (endIndex) + " of " + filteredData.current.length + " rows"
        tbody.current.innerHTML = ""

        for (let i = startIndex; i < endIndex; i++) {
            let tr = document.createElement("tr")
            columns.forEach((column: IColumn, index: number) => {
                if (!columnEnabledMap[column.name]) return
                let td = document.createElement("td")
                let value = filteredData.current[i][column.name]
                if (column.type === "number" || column.type === "usd") value = value.toLocaleString()
                if (column.type === "usd") value = "$" + value
                if (column.name == "County Link") value = ReactDOMServer.renderToString(<Link value={value} />)
                td.innerHTML = value
                td.style.textAlign = column.textAlign
                tr.appendChild(td)
            })
            tbody.current.appendChild(tr)
        }
    }

    function onTableNextClick() {
        if (currentRowIndex + amountOfRowsToDisplay >= filteredData.current.length) return
        let startIndex = currentRowIndex + amountOfRowsToDisplay
        let endIndex = currentRowIndex + amountOfRowsToDisplay * 2
        if (endIndex >= filteredData.current.length) displayRangeInHTMLTable(filteredData.current.length - amountOfRowsToDisplay - 1, filteredData.current.length)
        else displayRangeInHTMLTable(startIndex, endIndex)
    }

    function onTableBackClick() {
        if (currentRowIndex <= 0) return
        let newTableView = currentRowIndex - amountOfRowsToDisplay
        if (newTableView < 0) {
            displayRangeInHTMLTable(0, amountOfRowsToDisplay - 1)
            return
        }
        displayRangeInHTMLTable(newTableView, newTableView + amountOfRowsToDisplay)
    }

    function onRowSearchKeyUp(event: any) {
        let rowNumber = parseInt(event.target.value)
        if (rowNumber < 1) return
        if (!rowNumber) return
        if (rowNumber >= filteredData.current.length) displayRangeInHTMLTable(filteredData.current.length - amountOfRowsToDisplay, filteredData.current.length)
        else displayRangeInHTMLTable(rowNumber - 1, rowNumber + amountOfRowsToDisplay - 1)
    }

    function onRowAmountChange(event: any) {
        amountOfRowsToDisplay = parseInt(event.target.value)
        displayRangeInHTMLTable(currentRowIndex, currentRowIndex + amountOfRowsToDisplay)
    }

    function onFilterChanged() {
        let filterInputs = document.getElementsByClassName(style.filterInput)
        let filterValues: HTMLInputElement[] = Array.from(filterInputs).map((input: HTMLInputElement) => { return input })

        // const dataCheckedColumns = getDataWithoutDisabledColumns()
        const newFilteredData = data.current.filter(row => {
            let rowMatches = true
            filterValues.forEach(filter => {
                if (filter.value == "") return
                if (row[filter.id].toLowerCase().includes(filter.value.toLowerCase()) == false) rowMatches = false
            })
            return rowMatches
        })
        filteredData.current = newFilteredData
        displayRangeInHTMLTable(0, amountOfRowsToDisplay - 1)
    }

    // function getDataWithoutDisabledColumns(): any[] {
    //     let dataWithoutDisabledColumns: any[] = []
    //     data.current.forEach(row => {
    //         let newRow = []
    //         columns.forEach((column: IColumn, index: number) => {
    //             if (!columnEnabledMap[column.name]) return
    //             newRow.push(row[column.name])
    //         })
    //         dataWithoutDisabledColumns.push(newRow)
    //     })
    //     return dataWithoutDisabledColumns
    // }

    function onColumnMouseDown(event: any) {
        if (event.target.tagName != "TH") return
        selectedColumnIndex = Array.from(tableHeader.current.children).indexOf(event.target)
        tableHeader.current.style.cursor = 'grabbing'
        tableHeader.current.addEventListener('mousemove', onColumnDragged)
    }

    function onColumnDragged(event: any) {
        const cols = Array.from(tableHeader.current.children) as HTMLElement[]
        const mousePosition = { x: event.clientX, y: event.clientY }

        for (let i = 0; i < cols.length; i++) {
            const columnRect = cols[i].getBoundingClientRect()
            if (isMouseOverColumn(mousePosition, columnRect) && i != selectedColumnIndex) {
                if (i > selectedColumnIndex) cols[i].style.borderRight = '1px solid black'
                else cols[i].style.borderLeft = '1px solid black'
            }
            else cols[i].removeAttribute('style')
        }
    }

    function onColumnReleased(event: any) {
        if (event.target.tagName != "TH") return
        const cols = Array.from(tableHeader.current.children) as HTMLElement[]
        const newColumns = [...columns]
        const mousePosition = { x: event.clientX, y: event.clientY }

        for (let i = 0; i < cols.length; i++) {
            const columnRect = cols[i].getBoundingClientRect()
            if (isMouseOverColumn(mousePosition, columnRect)) {
                if (i == selectedColumnIndex) break
                if (i < selectedColumnIndex) {
                    newColumns.splice(i, 0, newColumns[selectedColumnIndex])
                    newColumns.splice(selectedColumnIndex + 1, 1)
                }
                else {
                    newColumns.splice(i + 1, 0, newColumns[selectedColumnIndex])
                    newColumns.splice(selectedColumnIndex, 1)
                }
                setColumns(newColumns)
                break
            }
        }
        tableHeader.current.style.cursor = 'unset'
        tableHeader.current.removeEventListener('mousemove', onColumnDragged)
    }

    function isMouseOverColumn(mousePosition: any, columnRect: any) {
        return mousePosition.x > columnRect.left && mousePosition.x < columnRect.right
    }

    function startResize(event: any) {
        const handle = event.target
        const index = Array.from(tableHeader.current.children).indexOf(handle.parentElement)
        const col = colgroup.current.children[index] as HTMLElement
        const initialWidth = col.clientWidth
        const initialX = parseInt(event.clientX)

        tableDiv.current.style.userSelect = 'none'

        document.addEventListener('mousemove', doResize)
        document.addEventListener('mouseup', stopResize)

        function doResize(event) {
            const newWidth = initialWidth + (event.clientX - initialX)
            col.style.width = `${newWidth}px`
        }
        function stopResize(event) {
            document.removeEventListener('mousemove', doResize)
            document.removeEventListener('mouseup', stopResize)
            tableDiv.current.style.userSelect = 'unset'
        }
    }

    function SortButton(props: any) {
        const button = useRef(null)

        useEffect(() => {
            const isLastSort = lastSort.current.column == props.column
            if (isLastSort && lastSort.current.ascending)
                button.current.innerText = ""

            else if (isLastSort && lastSort.current.ascending == false)
                button.current.innerText = ""

            else button.current.innerText = ""
        })

        return <button ref={button} className={style.sortButton} onClick={onSortButtonClicked}></button>

        function onSortButtonClicked() {
            if (button.current.innerText === "" && props.column.type == "number") {
                button.current.innerText = ""
                sortColumn(props.columnName, true, sortAsc)
            }
            else if (button.current.innerText === "" && props.column.type == "number") {
                button.current.innerText = ""
                sortColumn(props.columnName, false, sortDesc)
            }
            else if (button.current.innerText === "") {
                button.current.innerText = ""
                sortColumn(props.columnName, false, sortDesc)
            }
            else if (button.current.innerText === "") {
                button.current.innerText = ""
                sortColumn(props.columnName, true, sortAsc)
            }
        }
        function sortColumn(columnName: string, ascending: boolean, sort: (a: any, b: any, columnName: string) => any) {
            lastSort.current = { column: props.column, ascending: ascending, sort: sort }
            const sortedData = [...data.current]
            sortedData.sort((a, b) => { return sort(a, b, props.column.name) })
            data.current = [...sortedData]
            onFilterChanged()
        }
        function sortAsc(a: any, b: any, columnName: string) {
            if (a[columnName] > b[columnName]) return -1
            if (a[columnName] < b[columnName]) return 1
            return 0
        }
        function sortDesc(a: any, b: any, columnName: string) {
            if (a[columnName] < b[columnName]) return -1
            if (a[columnName] > b[columnName]) return 1
            return 0
        }
    }
}