import React, { useState, useEffect } from "react"
import * as style from "../styles/Alert.module.scss"

export default function Alert(props) {
    const [message, setMessage] = useState(props.message)

    useEffect(() => {
        window.addEventListener("mavQ:alert", handleAlertEvent)
        return () => {
            window.removeEventListener("mavQ:alert", handleAlertEvent)
        }
    }, [])

    return (
        <div id={ style.container }>
            <p id={ style.message }>{ message }</p>
        </div>
    )

    function handleAlertEvent(event) {
        const alert = document.getElementById(style.container)
        alert.style.transform = "translateY(120px)"
        setMessage(event.detail.message)
        setTimeout(() => {
            alert.style.transform = "translateY(-120px)"
        }, 3000)
    }
}